.footerContact {
  background-color: #27ae60;
  padding: 40px 0;
  color: #fff;
}
.footerContact h1 {
  font-size: 40px;
}
footer {
  background-color: #1d2636;
  padding: 50px 0;
  color: #fff;
}
footer .container {
  display: grid;
  grid-template-columns: auto auto;
  grid-gap: 70px;
}
footer .container .menu {
  display: grid;
  grid-template-columns: auto auto auto;
}
footer img {
  width: 150px;
}
footer h2 {
  font-weight: 500;
}
footer p {
  color: grey;
  margin: 20px 0;
}
footer input {
  background-color: #fff;
  padding: 17px;
  width: 100%;
  border-radius: 5px;
}
footer h3 {
  font-weight: 500;
  margin-bottom: 30px;
}
footer ul {
  display: block;
}
footer ul li {
  display: block;
  color: grey;
  margin-bottom: 20px;
}
.legal {
  text-align: center;
  padding: 20px;
  background: #1d2636;
  color: rgba(255, 255, 255, 0.5);
  border-top: 1px solid rgba(255, 255, 255, 0.1);
}

@media screen and (max-width: 970px) {
  .footerContact .container .send {
      display: flex;
      flex-direction: column;
      gap: 30px;
    }
    footer .container {
      display: grid;
      grid-template-columns: 1fr;
    }
}

@media screen and (max-width: 780px) {

}

@media screen and (max-width: 480px) {
  footer .container .menu {
    display: grid;
    grid-template-columns: 1fr;
  }
}
