.location .box {
  position: relative;
  border-radius: 5px;
}
.location img {
  border-radius: 5px;
}
.location .overlay {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  height: 250px;
  width: 100%;
  color: #fff;
  z-index: 222;
}
.location .overlay::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 340px;
  height: 210px;
  background: rgb(15 28 47 / 30%);
  z-index: -1;
  margin: 20px;
  border-radius: 5px;
}
.location h5 {
  font-size: 18px;
  font-weight: 500;
}
.location label {
  color: #fff;
  margin-right: 20px;
  opacity: 0.8;
}
.location .address {
  border-left: 5px solid orange;
  margin-bottom: 24px;
  padding-left: 10px;
  box-shadow: 2px 2px rgb(236, 233, 233);
  border-radius: 5px;
  display: flex;
  gap: 10px;
  justify-content: flex-start;
  align-items: center;
}

@media screen and (max-width: 480px) {
  .location .content.grid2 {
    grid-template-columns: 1fr;
  }
}
@media screen and (max-width: 780px) {
  .location .overlay::after {
    width: 280px;
    height: 170px;
  }
}